@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

* {
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
}

.add-btn {
	margin: 100px auto;
	display: block;
	width: 300px;
	font-size: 30px;
}

.wrapper {
	display: flex;
	justify-content: space-between;
	width: 80%;
	margin: 0 auto;
}

.left {
	flex: 0.25;
}

.list {
	border-top: 3px solid #0d6efd;
	box-shadow: 0px 0px 8px 0px rgba(34, 60, 80, 0.2);
	padding: 30px 0px;
}

.right {
	flex: 0.70;
	box-shadow: 0px 0px 8px 0px rgba(34, 60, 80, 0.2);
	padding: 40px;
	border-left: 3px solid #0d6efd;
}

.idea-title {
	text-align: center;
}

.idea {
	list-style-type: none;
	text-align: center;
	transition: 0.1s;
	border-radius: 4px;
	border-left: 0px solid #0d6efd;
    margin-top: 10px;
    line-height: 18px;
}

.ideas {
	margin: 0;
	padding: 15px;
}
.ideas-title {
    margin-top: 10px;
}
.idea-text {
	margin-top: 28px;
}

.idea:hover {
	border-left: 3px solid #0d6efd;
	cursor: pointer;
	margin-left: 5px;
}

.logo {
    font-size: 30px;
    font-weight: 700;
    color: #00349a;
    margin-top: 2px;
}

.navbar-brand {
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar {
    background: rgb(251, 254, 255) !important;
}

@media (max-width: 1495px) {
    .right {
        flex: 0.60;
    }
    .left {
        flex: 0.35;
    }
}

@media (max-width: 900px) {
    .list {
        padding: 10px 0px;
    }
    .wrapper {
        flex-direction: column;
    }
    .right {
        flex: 1;
        margin-top: 30px;
        padding: 20px;
    }
    .left {
        flex: 1;
    }
}