.form {
  width: 500px;
  padding: 60px;
  flex-direction: column;
  gap: 10px;
  border-bottom: 2px solid #003f9f;
  border-top: 2px solid #003f9f;
  box-shadow: 0px 0px 12px 10px rgba(0, 64, 109, 0.2);
  border-radius: 40px;
  background: white;
}
#warning {
  color: red;
  margin-top: -5px;
}
h2 {
  color: #0d6efd
}
@media (max-width: 600px) {
  .form {
      padding: 40px;
      height: 330px;
  }
}

.cursor-pointer {
  cursor: pointer;
}