.bg-ex-animation {
  background-image: linear-gradient(to right, rgba(93, 93, 252, 0.85) 0%, rgba(171, 138, 247, 0.85) 100%), url(https://static.vecteezy.com/system/resources/previews/000/106/564/original/vector-seamless-geometric-blue-pattern.jpg);
  background-position: 0 0;
  background-repeat: repeat;
  background-size: cover;
  animation: animated-bg 60s linear infinite;
  width: 100%;
  height: 350px;
}
@keyframes animated-bg {
  from { background-position: 0 0; }
  to { background-position: 0 1000px; }
}


.bg-ex-animation-1 {
  background-image: linear-gradient(to right, rgba(194, 194, 255, 0.85) 0%, rgba(171, 138, 247, 0.85) 100%), url(https://static.vecteezy.com/system/resources/previews/000/106/564/original/vector-seamless-geometric-blue-pattern.jpg);
  background-position: 0 0;
  background-repeat: repeat;
  background-size: cover;
  animation: animated-bg-1 60s linear infinite;
  width: 100%;
  height: 350px;
}
@keyframes animated-bg-1 {
  from { background-position: 0 0; }
  to { background-position: 0 1000px; }
}
